/* global console, document, Excel, Office */

// The initialize function must be run each time a new page is loaded
Office.onReady(() => {
  const urlInput = document.getElementById("urlInput") as any;
  urlInput.value = localStorage.getItem("sharedUrl") ?? "";
});

document.addEventListener("DOMContentLoaded", () => {
  const urlInput = document.getElementById("urlInput") as any;

  // Sauvegarder automatiquement l'URL dans le localStorage pendant la saisie
  urlInput?.addEventListener("input", () => {
    const url = urlInput.value.trim();
    if (url) {
      // eslint-disable-next-line no-undef
      Office.context.document.settings.set("apiUrl", url);
      Office.context.document.settings.saveAsync();
      localStorage.setItem("sharedUrl", url);
    }
  });
});

export async function run() {
  try {
    await Excel.run(async (context) => {
      /**
       * Insert your Excel code here
       */
      const range = context.workbook.getSelectedRange();

      // Read the range address
      range.load("address");

      // Update the fill color
      range.format.fill.color = "yellow";

      await context.sync();
      console.log(`The range address was ${range.address}.`);
    });
  } catch (error) {
    console.error(error);
  }
}
